import React from "react";
import style from "./DesktopPage.module.css";
import { Route, BrowserRouter } from "react-router-dom";

import HomeContainer from "../Home/HomeContainer";
import BookmarksContainer from "../Bookmarks/BookmarksContainer";
import ProfileContainer from "../Profile/ProfileContainer";
import MessagesContainer from "../Messages/MessagesContainer";
import MainHeaderContainer from "../MainHeader/MainHeaderContainer";

function Page(props) {
  return (
    <BrowserRouter>
      <div className={style.container}>
        <MainHeaderContainer />
        <div>
          <Route path="/home" render={() => <HomeContainer />} />
          <Route path="/messages" render={() => <MessagesContainer />} />
          <Route path="/bookmarks" render={() => <BookmarksContainer />} />
          <Route path="/@najmiddinyodgor" render={() => <ProfileContainer />} />
        </div>
      </div>
    </BrowserRouter>
  );
}

export default Page;
