import React from "react";
import style from "./Followers.module.css";
import { NavLink } from "react-router-dom";

const Followers = (props) => {
  if (props.path === undefined) {
    return (
      <span className={style.wrap}>
        <span className={style.count}>{props.followers} </span>
        <span className={style.name}>Followers</span>
      </span>
    );
  } else {
    return (
      <NavLink to={`/${props.path}`} className={style.link}>
        <span className={style.count}>{props.followers} </span>
        <span className={style.name}>Followers</span>
      </NavLink>
    );
  }
};

export default Followers;
