import React from "react";
import style from "./Name.module.css";
import { NavLink } from "react-router-dom";

const Name = (props) => {
  if (props.path === undefined) {
    return <span className={style.wrap}>{props.name}</span>;
  } else {
    return (
      <NavLink to={`/${props.path}`} className={style.link}>
        <span className={style.wrap}>{props.name}</span>
      </NavLink>
    );
  }
};

export default Name;
