import React from "react";

import Post from "../../Common/Post/Post";
import TweetContainer from "../../Common/Tweet/TweetContainer";

import Container from "../Page/Container/Container";
import Main from "../Page/Main/Main";
import Header from "../Page/Header/Header";
import SidebarContainer from "../Page/Sidebar/SidebarContainer";
import Content from "../Page/Content/Content";

const Home = (props) => {
  let posts = props.posts;
  let postsArray = posts.map((elem) => <Post post={elem} />);

  return (
    <Container>
      <Main>
        <Header title="Home" />
        <Content>
          <TweetContainer />
          {postsArray.reverse()}
        </Content>
      </Main>
      <SidebarContainer />
    </Container>
  );
};

export default Home;
