import React from "react";

import Container from "../Page/Container/Container";
import Main from "../Page/Main/Main";
import Header from "../Page/Header/Header";
import SidebarContainer from "../Page/Sidebar/SidebarContainer";
import Content from "../Page/Content/Content";
import Post from "../../Common/Post/Post";

const Bokmarks = (props) => {
  let bookmarks = props.bookmarks;
  let bookmarksArray = bookmarks.map((elem) => <Post post={elem} />);
  return (
    <Container>
      <Main>
        <Header title="Bookmarks" back="true" />
        <Content>{bookmarksArray.reverse()}</Content>
      </Main>
      <SidebarContainer />
    </Container>
  );
};

export default Bokmarks;
