import Tweet from "./Tweet";

import { connect } from "react-redux";

import {
  addNewPostCreater,
  updateNewPostTextCreator,
  updateNewPostImageCreator,
} from "../../../redux/postsReducer";

const mapStateToProps = (state) => {
  return {
    user: state.posts.user,
    newPost: state.posts.newPost,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    addNewPost: () => {
      dispatch(addNewPostCreater());
    },
    updateNewPostText: (text) => {
      dispatch(updateNewPostTextCreator(text));
    },
    updateNewPostImage: (image) => {
      dispatch(updateNewPostImageCreator(image));
    },
  };
};

const TweeContainer = connect(mapStateToProps, mapDispatchToProps)(Tweet);

export default TweeContainer;
