import React from "react";
import style from "./DialogInfo.module.css";

import Avatar from "../User/Avatar/Avatar";
import Name from "../User/Name/Name";
import Username from "../User/Username/Username";
import { NavLink } from "react-router-dom";
import Time from "../Time/Time";

const DialogInfo = (props) => {
  return (
    <NavLink
      to={`/messages/${props.dialog.id}`}
      className={style.link}
      activeClassName={style.link_active}
    >
      <div className={style.avatar}>
        <Avatar avatar={props.dialog.avatar} />
      </div>
      <div className={style.wrap}>
        <div className={style.name}>
          <Name name={props.dialog.name} />
        </div>
        <div className={style.username}>
          <Username username={props.dialog.username} />
        </div>
        <div className={style.message}>{props.dialog.messages[0].text}</div>
      </div>
      <div className={style.date}>
        <Time date={props.dialog.messages[0].date} />
      </div>
    </NavLink>
  );
};

export default DialogInfo;
