export const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case UPDATE_NEW_MESSAGE: {
      return {
        ...state,
        newMessage: action.messageText,
      };
    }
    case SEND_NEW_MESSAGE: {
      let stateCopy = { ...state };
      stateCopy.dialogs = [...state.dialogs];

      stateCopy.dialogs[0].messages.push({
        type: "outgoing",
        text: stateCopy.newMessage,
        date: Date.parse(new Date()),
      });

      stateCopy.newMessage = "";

      return stateCopy;
    }
    default:
      return state;
  }
};

const UPDATE_NEW_MESSAGE = "UPDATE_NEW_MESSSAGE";
const SEND_NEW_MESSAGE = "SEND_NEW_MESSAGE";

export const updateNewMessageCreator = (text) => {
  return { type: UPDATE_NEW_MESSAGE, messageText: text };
};

export const sendNewMessageCreator = () => {
  return { type: SEND_NEW_MESSAGE };
};
