import React from "react";
import style from "./Dialog.module.css";

import Header from "./Header/Header";
import Incoming from "./Incoming/Incoming";
import Outgoing from "./Outgoing/Outgoing";
import WriteAreaContainer from "./WriteArea/WriteAreaContainer";

import { hideScrollBar } from "../../../js/hideScrollBar";

const Dialog = (props) => {
  let messagesArray = props.user.messages;

  let messages = messagesArray.map((elem) => {
    if (elem.type === "incoming") {
      return <Incoming message={elem} />;
    } else {
      return (
        <div className={style.outgoing}>
          <Outgoing message={elem} />
        </div>
      );
    }
  });

  return (
    <section className={style.container}>
      <Header user={props.user} />
      <section className={style.messages} onWheel={hideScrollBar}>
        {messages}
      </section>
      <WriteAreaContainer />
    </section>
  );
};

export default Dialog;
