import React from "react";
import style from "./Post.module.css";

import Avatar from "../User/Avatar/Avatar";
import Name from "../User/Name/Name";
import Username from "../User/Username/Username";
import { NavLink } from "react-router-dom";
import Time from "../Time/Time";

const Post = (props) => {
  const createPostText = (text) => {
    if (text !== "" && text !== undefined) {
      return <div className={style.text}>{text}</div>;
    }
  };

  const createPostImage = (image) => {
    if (image !== "" && image !== undefined) {
      return (
        <div className={style.image}>
          <img src={image} alt="Content image" />
        </div>
      );
    }
  };

  const createLikes = (likes) => {
    let count = likes.count;
    if (count >= 1000) {
      count = (count / 1000).toFixed(1) + "K";
    }

    if (likes.isLiked) {
      return (
        <button
          className={`${style.btn} ${style.btn_like} ${style.btn_liked}`}
          type="button"
        >
          <span className={style.btn_icon}>
            <svg width="18.75px" height="18.75px" viewBox="0 0 24 24">
              <g>
                <path d="M12 21.638h-.014C9.403 21.59 1.95 14.856 1.95 8.478c0-3.064 2.525-5.754 5.403-5.754 2.29 0 3.83 1.58 4.646 2.73.814-1.148 2.354-2.73 4.645-2.73 2.88 0 5.404 2.69 5.404 5.755 0 6.376-7.454 13.11-10.037 13.157H12z"></path>
              </g>
            </svg>
          </span>
          <span className={style.value}>{count}</span>
        </button>
      );
    } else {
      return (
        <button className={`${style.btn} ${style.btn_like}`} type="button">
          <span className={style.btn_icon}>
            <svg width="18.75px" height="18.75px" viewBox="0 0 24 24">
              <g>
                <path d="M12 21.638h-.014C9.403 21.59 1.95 14.856 1.95 8.478c0-3.064 2.525-5.754 5.403-5.754 2.29 0 3.83 1.58 4.646 2.73.814-1.148 2.354-2.73 4.645-2.73 2.88 0 5.404 2.69 5.404 5.755 0 6.376-7.454 13.11-10.037 13.157H12zM7.354 4.225c-2.08 0-3.903 1.988-3.903 4.255 0 5.74 7.034 11.596 8.55 11.658 1.518-.062 8.55-5.917 8.55-11.658 0-2.267-1.823-4.255-3.903-4.255-2.528 0-3.94 2.936-3.952 2.965-.23.562-1.156.562-1.387 0-.014-.03-1.425-2.965-3.954-2.965z"></path>
              </g>
            </svg>
          </span>
          <span className={style.value}>{count}</span>
        </button>
      );
    }
  };

  const isAdded = (param) => {
    if (param) {
      return (
        <button
          className={`${style.btn} ${style.btn_add} ${style.btn_added}`}
          type="button"
        >
          <span className={style.btn_icon}>
            <svg width="18.75px" height="18.75px" viewBox="0 0 24 24">
              <g>
                <path d="M19.9 23.5c-.2 0-.3 0-.4-.1L12 17.9l-7.5 5.4c-.2.2-.5.2-.8.1-.2-.1-.4-.4-.4-.7V5.6c0-1.2 1-2.2 2.2-2.2h12.8c1.2 0 2.2 1 2.2 2.2v17.1c0 .3-.2.5-.4.7 0 .1-.1.1-.2.1z"></path>
              </g>
            </svg>
          </span>
          <span className={style.value}>Added to bookmarks</span>
        </button>
      );
    } else {
      return (
        <button className={`${style.btn} ${style.btn_add}`} type="button">
          <span className={style.btn_icon}>
            <svg width="18.75px" height="18.75px" viewBox="0 0 24 24">
              <g>
                <path d="M19.9 23.5c-.157 0-.312-.05-.442-.144L12 17.928l-7.458 5.43c-.228.164-.53.19-.782.06-.25-.127-.41-.385-.41-.667V5.6c0-1.24 1.01-2.25 2.25-2.25h12.798c1.24 0 2.25 1.01 2.25 2.25v17.15c0 .282-.158.54-.41.668-.106.055-.223.082-.34.082zM12 16.25c.155 0 .31.048.44.144l6.71 4.883V5.6c0-.412-.337-.75-.75-.75H5.6c-.413 0-.75.338-.75.75v15.677l6.71-4.883c.13-.096.285-.144.44-.144z"></path>
              </g>
            </svg>
          </span>
          <span className={style.value}>Add to bookmarks</span>
        </button>
      );
    }
  };

  return (
    <NavLink
      to={`/${props.post.username}/status/${props.post.post.id}`}
      className={style.container}
    >
      <article className={style.wrap}>
        <header className={style.header}>
          <div className={style.avatar}>
            <Avatar avatar={props.post.avatar} path={props.post.username} />
          </div>
          <div className={style.name}>
            <Name name={props.post.name} path={props.post.username} />
          </div>
          <div className={style.username}>
            <Username username={props.post.username} />
          </div>
          <div className={style.time}>
            <Time date={props.post.post.date} />
          </div>
        </header>
        <main className={style.content}>
          {createPostText(props.post.post.text)}
          {createPostImage(props.post.post.image)}
        </main>
        <footer>
          {createLikes(props.post.post.likes)}
          {isAdded(props.post.post.isAdded)}
        </footer>
      </article>
    </NavLink>
  );
};

export default Post;
