import React from "react";
import style from "./Avatar.module.css";
import defaultImage from "../../../../img/default_user_image.jpg";

import { NavLink } from "react-router-dom";

const Avatar = (props) => {
  let avatar;

  if (props.avatar === undefined) {
    avatar = `${defaultImage}`;
  } else {
    avatar = props.avatar;
  }

  if (props.path === undefined) {
    return (
      <div className={style.wrap}>
        <img src={avatar} alt />
      </div>
    );
  } else {
    return (
      <NavLink to={`/${props.path}`} className={style.link}>
          <img src={avatar} alt />
      </NavLink>
    );
  }
};

export default Avatar;
