import React from "react";
import style from "./WriteArea.module.css";

import { autoGrowTextArea } from "../../../../js/autoGrowTextArea";
import { hideScrollBar } from "../../../../js/hideScrollBar";

const WritingField = (props) => {
  const textRef = React.createRef();
  const submitRef = React.createRef();

  const updateNewMessage = () => {
    props.updateNewMessage(textRef.current.value);
  };

  const sendNewMessage = () => {
    props.sendNewMessage();
  };

  return (
    <section className={style.container}>
      <div className={style.wrap}>
        <div className={style.message}>
          <textarea
            className={style.message_text}
            name="message_text"
            aria-label="Write message"
            data-scroll="true"
            onKeyUp={autoGrowTextArea}
            onWheel={hideScrollBar}
            onChange={updateNewMessage}
            value={props.value}
            ref={textRef}
            placeholder="Start a new message"
          ></textarea>
        </div>
        <button
          className={`${style.btn} ${style.btn_send}`}
          aria-label="Send"
          type="button"
          onClick={sendNewMessage}
          ref={submitRef}
        >
          <span className={style.icon}>
            <svg viewBox="0 0 24 24">
              <g>
                <path d="M21.13 11.358L3.614 2.108c-.29-.152-.64-.102-.873.126-.23.226-.293.577-.15.868l4.362 8.92-4.362 8.92c-.143.292-.08.643.15.868.145.14.333.212.523.212.12 0 .24-.028.35-.087l17.517-9.25c.245-.13.4-.386.4-.664s-.155-.532-.4-.662zM4.948 4.51l12.804 6.762H8.255l-3.307-6.76zm3.307 8.26h9.498L4.948 19.535l3.307-6.763z"></path>
              </g>
            </svg>
          </span>
        </button>
      </div>
    </section>
  );
};

export default WritingField;
