import React from "react";
import style from "./Tweet.module.css";

import Avatar from "../User/Avatar/Avatar";

import { autoGrowTextArea } from "../../../js/autoGrowTextArea";
import { drawProgress } from "../../../js/drawProgress";

const Tweet = (props) => {
  const textRef = React.createRef();
  const imageRef = React.createRef();
  const progressRef = React.createRef();

  const addNewPost = () => {
    props.addNewPost();
  };

  const updateNewPostText = () => {
    drawProgress(textRef.current.value.length, progressRef.current);
    props.updateNewPostText(textRef.current.value);
  };

  const updateNewPostImage = () => {
    if (imageRef.current.files[0] !== undefined) {
      let imageSrc;
      let fReader = new FileReader();
      fReader.onloadend = function (event) {
        imageSrc = event.target.result;
        props.updateNewPostImage(imageSrc);
      };
      fReader.readAsDataURL(imageRef.current.files[0]);
    }
  };

  const cancelImage = () => {
    props.updateNewPostImage("");
  };

  const drawImage = (image) => {
    if (image) {
      return (
        <div className={style.image}>
          <img src={image} />
          <button className={style.cancel} onClick={cancelImage}>
            <span>
              <svg
                width="19.5px"
                height="19.5px"
                fill="#FFF"
                viewBox="0 0 24 24"
              >
                <g>
                  <path d="M13.414 12l5.793-5.793c.39-.39.39-1.023 0-1.414s-1.023-.39-1.414 0L12 10.586 6.207 4.793c-.39-.39-1.023-.39-1.414 0s-.39 1.023 0 1.414L10.586 12l-5.793 5.793c-.39.39-.39 1.023 0 1.414.195.195.45.293.707.293s.512-.098.707-.293L12 13.414l5.793 5.793c.195.195.45.293.707.293s.512-.098.707-.293c.39-.39.39-1.023 0-1.414L13.414 12z"></path>
                </g>
              </svg>
            </span>
          </button>
        </div>
      );
    }
  };

  const selectImage = () => {
    imageRef.current.click();
  };

  return (
    <section className={style.container}>
      <div className={style.avatar}>
        <Avatar avatar={props.user.avatar} path={props.user.username} />
      </div>
      <div className={style.wrap}>
        <div className={style.message}>
          <textarea
            className={style.message_content}
            name="message_text"
            aria-label="Write message"
            minLength="0"
            maxLength="280"
            value={props.newPost.text}
            placeholder="What's happening?"
            onKeyUp={autoGrowTextArea}
            onChange={updateNewPostText}
            ref={textRef}
          ></textarea>
          {drawImage(props.newPost.image)}
        </div>
        <div className={style.inner_wrap}>
          <button
            className={`${style.btn} ${style.btn_image}`}
            aria-label="Add photo"
            type="button"
            onClick={selectImage}
          >
            <input
              className={style.input}
              type="file"
              accept="image/png, image/jpeg, image/webp, image/gif"
              name="message_image"
              onChange={updateNewPostImage}
              ref={imageRef}
              multiple
            />
            <span className={style.icon}>
              <svg viewBox="0 0 24 24">
                <g>
                  <path d="M19.75 2H4.25C3.01 2 2 3.01 2 4.25v15.5C2 20.99 3.01 22 4.25 22h15.5c1.24 0 2.25-1.01 2.25-2.25V4.25C22 3.01 20.99 2 19.75 2zM4.25 3.5h15.5c.413 0 .75.337.75.75v9.676l-3.858-3.858c-.14-.14-.33-.22-.53-.22h-.003c-.2 0-.393.08-.532.224l-4.317 4.384-1.813-1.806c-.14-.14-.33-.22-.53-.22-.193-.03-.395.08-.535.227L3.5 17.642V4.25c0-.413.337-.75.75-.75zm-.744 16.28l5.418-5.534 6.282 6.254H4.25c-.402 0-.727-.322-.744-.72zm16.244.72h-2.42l-5.007-4.987 3.792-3.85 4.385 4.384v3.703c0 .413-.337.75-.75.75z"></path>
                  <circle cx="8.868" cy="8.309" r="1.542"></circle>
                </g>
              </svg>
            </span>
          </button>
          <div className={style.progress}>
            <svg height="100%" viewBox="0 0 20 20" width="100%">
              <circle
                cx="50%"
                cy="50%"
                fill="none"
                strokeWidth="2"
                r="9"
                stroke="#E6ECF0"
              ></circle>
              <circle
                cx="50%"
                cy="50%"
                fill="none"
                strokeWidth="2"
                r="9"
                stroke="#1DA1F2"
                strokeLinecap="round"
                ref={progressRef}
              ></circle>
            </svg>
          </div>
          <button className={style.submit} type="button" onClick={addNewPost}>
            Tweet
          </button>
        </div>
      </div>
    </section>
  );
};

export default Tweet;
