import React from "react";
import style from "./UserToFollow.module.css";

import Name from "../User/Name/Name";
import Username from "../User/Username/Username";
import Avatar from "../User/Avatar/Avatar";
import Bio from "../User/Bio/Bio";

import { NavLink } from "react-router-dom";

const UserToFollow = (props) => {
  const createButton = (param) => {
    if (param) {
      return (
        <button className={`${style.btn} ${style.btn_active}`}>
          Following
        </button>
      );
    } else {
      return <button className={style.btn}>Follow</button>;
    }
  };

  if (props.mini === "true") {
    return (
      <NavLink to={props.user.username} className={style.container}>
        <div className={style.avatar}>
          <Avatar path={props.user.username} avatar={props.user.avatar} />
        </div>
        <div className={style.wrap}>
          <div className={`${style.col} ${style.col1}`}>
            <div className={style.name}>
              <Name path={props.user.username} name={props.user.name} />
            </div>
            <div className={style.username}>
              <Username
                path={props.user.username}
                username={props.user.username}
              />
            </div>
          </div>
          <div className={style.col}>{createButton(props.user.isFollowed)}</div>
        </div>
      </NavLink>
    );
  } else {
    return (
      <NavLink to={props.user.username} className={style.container}>
        <div className={style.avatar}>
          <Avatar path={props.user.username} avatar={props.user.avatar} />
        </div>
        <div className={style.wrap}>
          <div className={style.col}>
            <div className={style.name}>
              <Name path={props.user.username} name={props.user.name} />
            </div>
            <div className={style.username}>
              <Username
                path={props.user.username}
                username={props.user.username}
              />
            </div>
          </div>
          <div className={style.col}>{createButton(props.user.isFollowed)}</div>
        </div>
        <Bio bio={props.user.bio} />
      </NavLink>
    );
  }
};

export default UserToFollow;
