import React from "react";

import Header from "../Page/Header/Header";
import Content from "../Page/Content/Content";
import Container from "../Page/Container/Container";
import Post from "../../Common/Post/Post";

const Bookmarks = (props) => {
  let bookmarks = props.bookmarks;
  let bookmarksArray = bookmarks.map((elem) => <Post post={elem} />);
  return (
    <Container>
      <Header user={props.user} title="Bookmarks" />
      <Content border="true">{bookmarksArray.reverse()}</Content>
    </Container>
  );
};

export default Bookmarks;
