import React from "react";

import Header from "../Page/Header/Header";
import Content from "../Page/Content/Content";
import Container from "../Page/Container/Container";
import DialogInfo from "../../Common/DialogInfo/DialogInfo";

const Messages = (props) => {
  let dialogsArray = props.dialogs;

  let dialogsInfo = dialogsArray.map((elem) => <DialogInfo dialog={elem} />);

  return (
    <Container>
      <Header user={props.user} title="Messages" />
      <Content>{dialogsInfo}</Content>
    </Container>
  );
};

export default Messages;
