import React from "react";
import style from "./Follow.module.css";

import UserToFollow from "../UserToFollow/UserToFollow";
import { NavLink } from "react-router-dom";

const Follow = (props) => {
  let usersList = props.users.map((elem) => (
    <div className={style.wrap}>
      <UserToFollow mini="true" user={elem} />
    </div>
  ));

  return (
    <section className={style.container}>
      <h2 className={style.title}>Who to follow</h2>
      <div className={style.content}>
        {usersList[0]}
        {usersList[1]}
        {usersList[2]}
      </div>
      <NavLink to="/home" className={style.more}>
        Show more
      </NavLink>
    </section>
  );
};

export default Follow;
