export function autoGrowTextArea (evt) {
  let elem = evt.target;
  if (elem.clientHeight < elem.scrollHeight) {
    elem.style.height = elem.scrollHeight + "px";
    if (elem.clientHeight < elem.scrollHeight) {
      elem.style.height =
        elem.scrollHeight * 2 - elem.clientHeight + "px";
    }
  }
};
