const ADD_NEW_POST = "ADD_NEW_POST";
const UPDATE_NEW_POST_TEXT = "UPDATE_NEW_POST_TEXT";
const UPDATE_NEW_POST_IMAGE = "UPDATE_NEW_POST_IMAGE";

export const postsReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_NEW_POST: {
      let stateCopy = { ...state };
      stateCopy.user = { ...state.user };
      stateCopy.list = [...state.list];
      stateCopy.newPost = { ...state.newPost };

      stateCopy.list.push({
        avatar: stateCopy.user.avatar,
        name: stateCopy.user.name,
        lastName: stateCopy.user.lastName,
        username: stateCopy.user.username,
        id: stateCopy.user.id,
        post: {
          id: Math.random() * 1000000,
          date: Date.parse(new Date()),
          text: stateCopy.newPost.text,
          image: stateCopy.newPost.image,
          likes: {
            count: 0,
            isLiked: false,
          },
          isAdded: false,
        },
      });

      stateCopy.newPost.text = "";
      stateCopy.newPost.image = "";

      return stateCopy;
    }

    case UPDATE_NEW_POST_TEXT: {
      let stateCopy = { ...state };
      stateCopy.newPost = { ...state.newPost };

      stateCopy.newPost.text = action.postText;

      return stateCopy;
    }

    case UPDATE_NEW_POST_IMAGE: {
      let stateCopy = { ...state };
      stateCopy.newPost = { ...state.newPost };

      stateCopy.newPost.image = action.postImage;

      return stateCopy;
    }

    default:
      return state;
  }
};

export const addNewPostCreater = () => ({
  type: ADD_NEW_POST,
});

export const updateNewPostTextCreator = (text) => {
  return { type: UPDATE_NEW_POST_TEXT, postText: text };
};

export const updateNewPostImageCreator = (image) => {
  return { type: UPDATE_NEW_POST_IMAGE, postImage: image };
};
