import React from "react";
import style from "./Time.module.css";

const Time = (props) => {
  const createDate = (param) => {
    let date = new Date(param);
    let currentDate = new Date();

    let pastSecunds = (Date.parse(currentDate) - Date.parse(date)) / 1000;
    let pastMinutes = pastSecunds / 60;
    let pastHours = pastMinutes / 60;

    let year = date.getFullYear();
    let month = date.getMonth();
    let day = date.getDate();
    let hours = date.getHours();
    let minutes = date.getMinutes();
    if (minutes < 10) {
      minutes += "0";
    }

    let monthArray = [
      "Jan",
      "Feb",
      "March",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec",
    ];

    let info = `${monthArray[month]} ${day}, ${year} ${hours}:${minutes}`;

    if (props.info) {
      return (
        <time className={`${style.time} ${style.time_info}`} date={info}>
          {info}
        </time>
      );
    }
    if (pastSecunds < 60) {
      return (
        <time className={style.time} date={info} title={info}>
          {pastSecunds}s
        </time>
      );
    } else if (pastMinutes < 60) {
      return (
        <time className={style.time} date={info} title={info}>
          {pastMinutes}m
        </time>
      );
    } else if (pastHours < 24) {
      return (
        <time className={style.time} date={info} title={info}>
          ${pastHours}h
        </time>
      );
    } else {
      return (
        <time className={style.time} date={info} title={info}>
          {monthArray[month - 1]} {day}
        </time>
      );
    }
  };

  return createDate(props.date);
};

export default Time;
